<template>
  <!-- labelvalueradio-groupmodelmodel-->
  <label class="fe-radio" :class="{'is-checked': label === model}">
    <span class="fe-radio__input">
      <span class="fe-radio__inner"></span>
      <!-- radio  -->
      <!-- inputinputv-model="value"value -->
      <input
        type="radio"
        class="fe-radio__original"
        :value="label"
        :name="name"
        v-model="model"
      />
    </span>
    <span class="fe-radio__label">
      <slot></slot>
      <!-- radio label -->
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>

<script>
export default {
  name: 'FeRadio',
  props: {
    label: {
      type: [String, Number, Boolean],
      default: ''
    },
    // v-model
    value: null,
    name: {
      type: String,
      default: ''
    }
  },
  inject: {
    RadioGroup: {
      //  radioradio-group
      default: ''
    }
  },
  // 
  // model
  // model
  // getset
  computed: {
    model: {
      get() {
        // radioradioGroupthis.value
        // radioradioGroupthis.RadioGroup.value
        return this.isGroup ? this.RadioGroup.value : this.value
      },
      set(value){
        // input
        this.isGroup ? this.RadioGroup.$emit('input', value) : this.$emit('input', value)
      }
    },
    isGroup () {
      // radioradioGroup,!!:
      return !!this.RadioGroup
    }
  },
}
</script>
