<template>
  <div class="fe-switch" @click="handleClick" :class="{'is-checked': value}">
    <!-- switchnameinputname-->
    <input class="fe-switch__input"
      type="checkbox"
      :name="name"
      ref="checkbox"
    />
    <span class="fe-switch__core" ref="core">
      <span class="fe-switch__button"></span>
    </span>
  </div>
</template>

<script>
export default {
  name: 'FeSwitch',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    activeColor: {
      type: String,
      default: ''
    },
    inactiveColor: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    }
  },
  mounted() {
    // 
    this.setColor()
    // CheckBox
    this.$refs.checkbox.checked = this.value
  },
  methods: {
    //  input 
    async handleClick() {
      this.$emit('input', !this.value)
      // 
      // valuesetColorcolor
      // dom
      await this.$nextTick()
      this.setColor()
      // CheckBox
      this.$refs.checkbox.checked = this.value
    },
    setColor() {
      if (this.activeColor || this.inactiveColor) {
        // valuetrueactiveColorinactiveColor
        let color = this.value ? this.activeColor : this.inactiveColor
        this.$refs.core.style.borderColor = color
        this.$refs.core.style.backgroundColor = color
      }
    },
  },
}
</script>