<template>
  <div class="fe-input" :class="{'fe-input__suffix': showSuffix}">
    <!-- -showPassword -->
    <!--  text type -->
    <!-- show-password,   -->
    <input
      :class="['fe-input__inner', {'is-disabled': disabled}]"
      :placeholder="placeholder"
      :type="showPassword ? (passwordVisible ? 'text' : 'password') : type"
      :name="name"
      :disabled="disabled"
      :value="value"
      @input="handleInput">
      <span class="fe-input__suffix" v-if="showSuffix">
        <i class="fe-input__icon fe-icon-shanchu" v-if="clearable && value" @click="clear"></i>
        <!--   -->
        <i
          class="fe-input__icon fe-icon-yanjing"
          :class="{'fe-icon-yanjing-active': passwordVisible}"
          v-if="showPassword && type ==='password'"
          @click="handlePassword"
        >
        </i>
      </span>
  </div>
</template>

<script>
export default {
  name: 'FeInput',
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'text'
    },
    name: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    value: {
      type: String,
      default: ''
    },
    clearable: {
      type: Boolean,
      default: false
    },
    showPassword: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // 
      passwordVisible: false
    }
  },
  computed: {
    //  
    showSuffix() {
      return this.clearable || this.showPassword
    }
  },
  methods: {
    handleInput(e) {
      // inputv-model
      this.$emit('input',e.target.value)
    },
    clear() {
      // 
      this.$emit('input','')
    },
    handlePassword() {
      // 
      this.passwordVisible = !this.passwordVisible
    }
  }
}
</script>